import { useContext } from "react";
import "../otpmodal.css";
import ProductContext from "../contexts/products/ProductContext";


const OtpModal = (props) => {
  const context = useContext(ProductContext); 
  const { otpValue, changeOtpInput } = context;
  const { isOpened, contact, changeNumber, verifyOtpAndSubmit } = props;
  return (
    <>
      {isOpened && (
        <>
          <div id="otpModal">
            <center>
              <i className="fa-solid fa-circle-check"></i>
            </center>
            <p className="topModalMsg">
              An OTP has been successfully sent to <span className="number">{contact}</span>
              {/* <i
                title="Change number"
                className="fas fa-edit"
                onClick={changeNumber}
              ></i> */}
            </p>
            <input
              minLength={6}
              maxLength={6}
              name="otpInput"
              value={otpValue}
              type="number"
              className="inputFields otpInput"
              id="otpInput"
              onChange={changeOtpInput}
              placeholder="Enter OTP code"
            />
            <button
              //   onClick={validateNumber}
              className="validateOptBtn"
              onClick={verifyOtpAndSubmit}
            >
              Verify mobile number
            </button>
          </div>
          <div id="otpModalBg"></div>
        </>
      )}
    </>
  );
};

export default OtpModal;
