import ProductContext from "./ProductContext";
import { useState } from "react";
import { authentictaion } from "../../firebase-config";
import { RecaptchaVerifier, signInWithPhoneNumber } from "firebase/auth";

const ProductState = props => {
  // api host
  const host = "https://www.typingbooster.tech";
  // const host = "http://localhost:5000";

  // Sorting method for serach results
  // const [sorting, setSorting] = useState(null);
  // Paths for images
  const userPicturePath = host + "/Images/users/";
  const productPicturePath = host + "/Images/products/";
  // States
  const [user, setUser] = useState(null);
  // const [detailProductId, setDetailProductId] = useState(null);
  // const [searchQuery, setSearchQuery] = useState(null)
  // Alert controlling
  const [alertMessage, setAlertMessage] = useState(null);

  const dismissAlert = () => {
    setAlertMessage(null);
  };

  const showAlert = msg => {
    setAlertMessage(msg);
    setTimeout(() => {
      dismissAlert();
    }, 4000);
  };

  // API related functions
  const fetchUser = () => {
    const authToken = localStorage.getItem("auth-token");
    if (authToken) {
      // console.log(authToken);
      // fetching user through api
      // API request for user's self information
      fetch(`${host}/auth/getSelfInfo/`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "auth-token": authToken
        }
      })
        .then(res => {
          return res.json();
        })
        .then(result => {
          // console.log(result);
          if (result.success) {
            setUser(result.user);
            // console.log(user);
          }
        });
    }
  };

  const notifyUser = (message, notifiyingUser) => {
    // console.log(notifiyingUser);
    fetch(`${host}/auth/addNotification`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem("auth-token")
      },
      body: JSON.stringify({
        id: notifiyingUser._id,
        notification: message
      })
    })
      .then(res => {
        return res.json();
      })
      .then(result => {
        if (result.success) {
          return true;
        }
      });
  };

  // OTP Verification states and functions
  const [otpValue, setOtpValue] = useState("");

  const changeOtpInput = e => {
    // console.log(e.target.value);
    setOtpValue(e.target.value);
  };

  const generateRecaptcha = () => {
    window.recaptchaVerifier = new RecaptchaVerifier(
      "recaptcha-container",
      {
        size: "invisible",
        callback: response => {
          // reCAPTCHA solved, allow signInWithPhoneNumber.
        }
      },
      authentictaion
    );
  };

  // Send OTP function
  const sendOtp = num => {
    if(/^[0-9]{10}$/.test(num)){
      num = '+91' + num;
    }
    // console.log(num, typeof(num));
    if(!(/^\+91[0-9]{10}$/.test(num))){
      showAlert("Contact Number is not in proper format")
      return false
    }
    generateRecaptcha();
    let appVerifier = window.recaptchaVerifier;
    signInWithPhoneNumber(authentictaion, num, appVerifier)
      .then(conRes => {
        window.confirmationResult = conRes;
        return true
      })
      .catch(e => {
        console.log(e);
        appVerifier.clear()
      });
      return true
  };

  // const verifyOtp = () => {
    // console.log(otpValue);
    // if(otpValue.length === 6){
    //   let confirmationResult = window.confirmationResult
    //   confirmationResult
    //     .confirm(otpValue)
    //     .then(result => {
    //       // User signe in successfully.
          // const user = result.user;
    //       console.log(result);
    //       return true
    //     })
    //     .catch(error => {
    //       // User couldn't sign in (bad verification code?)
    //       console.log(error);
    //       // return false
    //     });
    // }
  // };

  return (
    <ProductContext.Provider
      value={{
        alertMessage,
        showAlert,
        dismissAlert,
        host,
        user,
        setUser,
        fetchUser,
        userPicturePath,
        productPicturePath,
        notifyUser,
        otpValue,
        changeOtpInput,
        sendOtp
      }}
    >
      {props.children}
    </ProductContext.Provider>
  );
};

export default ProductState;
