import { useState, useContext, useEffect } from "react";
import ProductContext from "../contexts/products/ProductContext";
import ListItem from "./ListItem";
import InfiniteScroll from "react-infinite-scroll-component";
import Loader from "./Loader";
import emptyWishlist from "../assests/Images/emptyWishlist.png";
import { useHistory } from "react-router-dom";

const Results = (props) => {
  const context = useContext(ProductContext);
  const { user, showAlert, host } = context;

  const history = useHistory();

  const [products, setProducts] = useState([]);
  const [totalResults, setTotalResults] = useState(0);
  const [loading, setLoading] = useState(true);
  const [starting, setStarting] = useState(0);
  const [success, setSuccess] = useState(false);

  const fetchMore = async () => {
    setLoading(true);

    const res = await fetch(`${host}/products/getWishlistProducts`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem("auth-token")
      },
      body: JSON.stringify({ startFrom: starting })
    });

    const result = await res.json();
    // console.log(result);
    if (result.success) {
      setSuccess(true);
      if (result.resultsCount > 0) {
        setProducts(products.concat(result.products).filter((e)=>{
          return e;
        }));
        setTotalResults(result.resultsCount);
        setStarting(starting + 5);
      }
      setLoading(false);
    } else {
      showAlert(result.message);
    }
  };

  useEffect(() => {
    // eslint-disable-next-line
    let authToken = localStorage.getItem("auth-token");
    if (!authToken) {
      history.push("/login");
    }
    if (!success) {
      fetchMore();
      // console.log("Fetching");
    }
  }, []);

  return (
    <>
      {products.length !== 0 ? (
        <div className="listItemsContainer">
          <InfiniteScroll
            dataLength={products.length} //This is important field to render the next data
            next={fetchMore}
            hasMore={totalResults !== products.length}
            loader={<Loader />}
          >
            <Loader toLoad={loading} />
            <p className="wishlistHeading">My wishlist <i className="fa fa-shopping-cart"></i></p>
            {products.map((productItem) => {
              if (productItem) {
                // console.log(productItem);
                // if (productItem.availability === "available") {
                  return (
                    <ListItem key={productItem._id} productItem={productItem} />
                  );
                // }
              }
              return null;
            })}
          </InfiniteScroll>
        </div>
      ) : (
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            padding: "5rem"
          }}
        >
          <img
            className="emptyWishlist"
            src={emptyWishlist}
            alt="Wishlist is empty"
          />
        </div>
      )}
    </>
  );
};

export default Results;
