import React from 'react'

const Compressor = (props) => {
  return (
    <div style={{ marginTop: "4px", fontWeight: "500", textAlign: props.align?props.align:"start" }} className="labelGroup">
      <strong className={props.mode}>Compressor - </strong>{" "}
      <a
        target="_blank"
        href="https://www.iloveimg.com/compress-image"
        className={props.mode + "A compressorAnchor"}
      >
        Compress your large size images here
      </a>
    </div>
  );
}

export default Compressor