import ProductContext from "../contexts/products/ProductContext";
import { useContext } from "react";
import { useHistory } from "react-router-dom";

const ProductCard = (props) => {
  const context = useContext(ProductContext);
  const { productPicturePath } = context;
  const history = useHistory()

  const { productItem, deleteProduct } = props;

  return (
    <div className="sellingCard">
      <img
        className="cardImg"
        src={productPicturePath + productItem.productImages[0]}
        alt="titlePicture"
      />
      <div className="cardInfo">
        <div className="cardItemName"> {productItem.name} </div>
        <span className="cardPriceDetails">{productItem.sellingPrice}</span>
        <span className="cardDetailMRP">{productItem.actualPrice}</span>
        <p className="listDetails">Availability: {productItem.availability}</p>
        <p className="listDetails">Quantity: {productItem.quantity}</p>
        <p className="listDetails" style={{ paddingBottom: "4px" }}>
          Listed on:{" "}
          {new Date(Number(productItem.date)).getDate() +
            "/" +
            Number(new Date(Number(productItem.date)).getMonth() + 1) +
            "/" +
            new Date(Number(productItem.date)).getFullYear()}
        </p>
        <p className="cardListDesc">
          {productItem.description.length > 60 ? productItem.description.slice(0, 60) + "..." : productItem.description}
        </p>
        <div className="btns">
          <button onClick={()=>{history.push("/register-your-product?update=" + productItem._id);}} className="btnCard btnEdit">Edit</button>
          <button className="btnCard btnDelete" onClick={()=>{deleteProduct(productItem._id, productItem.name)}}>Delete</button>
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
