import loading from "../assests/Images/Loader.gif"

export default function Spinner(props) {
    return (
        <div className={props.propsClass||''} style={{textAlign: "center", minHeight: "1rem", background: "transparent"}} >
            {props.toLoad && <img style={{ height: "4rem" }} src={loading} alt="Loading" />}
        </div>
    )
}
