import "../sellings.css";
import ProductCard from "./ProductCard";
import { useState, useEffect, useContext } from "react";
import ProductContext from "../contexts/products/ProductContext";
import { useHistory } from "react-router-dom";
import InfiniteScroll from "react-infinite-scroll-component";
import Loader from "./Loader";
import emptyList from "../assests/Images/No_results/noItemsOnSale.svg";

const Sellings = () => {
  const context = useContext(ProductContext);
  const { user, host, showAlert } = context;
  const [products, setProducts] = useState([]);
  const history = useHistory();
  const [starting, setStarting] = useState(0);
  const [success, setSuccess] = useState(false);
  const [totalResults, setTotalResults] = useState(0);
  const [loading, setLoading] = useState(true);
  const resultsToShow = 6;

  const fetchMore = async (toStart) => {
    setLoading(true);
    const res = await fetch(
      `${host}/products/getProductsBySeller/${user._id}`,
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("auth-token")
        },
        body: JSON.stringify({
          startFrom: starting,
          limit: resultsToShow,
          availability: "all"
        })
      }
    );

    const result = await res.json();
    // console.log(result);
    if (result.success) {
      setSuccess(true);
      if (result.resultsCount > 0) {
        setProducts(products.concat(result.products));
        setTotalResults(result.resultsCount);
        setStarting(starting + resultsToShow);
      }
      setLoading(false);
    } else {
      showAlert(result.message);
    }
  };

  useEffect(() => {
    let authToken = localStorage.getItem("auth-token");
    if (!authToken) {
      history.push("/login");
    }
    if (!success && user) {
      fetchMore();
    }
  }, [user]);

  const deleteProduct = (id, name) => {
    const isConfrim = window.confirm(`You are about to delist ${name} permanently?`);
    if (isConfrim) {
      fetch(`${host}/products/deleteproduct/${id}`, {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
          "auth-token": localStorage.getItem("auth-token")
        }
      })
        .then((res) => {
          return res.json();
        })
        .then((result) => {
          // console.log(result);
          if (result.success) {
            setProducts(products.filter((ele)=>{
              return ele._id !== id;
            }))
          }
          showAlert(result.message);
        });
    }
  };

  return (
    <>
      {products.length !== 0 ? (
        <div className="sellingsMain">
          <InfiniteScroll
            dataLength={products.length} //This is important field to render the next data
            next={fetchMore}
            hasMore={totalResults !== products.length}
            loader={<Loader />}
          >
            <Loader toLoad={loading} />
            <div className="sellingsCont">
              <h2 style={{ color: "#f9f9f9" }} className="expMoreHeadig sellingsListHeading">
                My products' list
              </h2>
              <div className="cards">
                {products.map((productItem) => {
                  if (productItem) {
                    return (
                      <ProductCard
                        key={productItem._id}
                        productItem={productItem}
                        deleteProduct={deleteProduct}
                      />
                    );
                  }
                  return null;
                })}
              </div>
            </div>
          </InfiniteScroll>
        </div>
      ) : (
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            padding: "5rem"
          }}
        >
          <img
            className="emptyWishlist"
            src={emptyList}
            alt="Wishlist is empty"
          />
        </div>
      )}
    </>
  );
};

export default Sellings;
