import "../recoverPassword.css";
import { useState, useContext } from "react";
import logo from "../assests/Images/logo.png";
import ProductContext from "../contexts/products/ProductContext";
import { useHistory } from "react-router-dom";

const RecoverPassword = () => {
  const context = useContext(ProductContext);
  const { host, showAlert } = context;
  const history = useHistory();

  const [newCredentials, setNewCredentials] = useState({
    otpCode: "",
    newPassword: "",
    confirmPassword: ""
  });

  const [isEmailSent, setIsEmailSent] = useState(false);

  const changeDetails = e => {
    setNewCredentials({ ...newCredentials, [e.target.name]: e.target.value });
  };

  const seePassword = e => {
    let passwordField = e.target.parentNode.children[0];
    // console.log(passwordField);
    if (e.target.classList.contains("fa-eye-slash")) {
      e.target.classList.replace("fa-eye-slash", "fa-eye");
      passwordField.type = "text";
    } else {
      e.target.classList.replace("fa-eye", "fa-eye-slash");
      passwordField.type = "password";
    }
  };

  const toggleRecoveryOption = () => {
    setIsEmailSent(!isEmailSent);
  };

  const sendOtpEmailAction = (e)=>{
    e.preventDefault()
    // Send OTP api
    fetch(`${host}/auth/sendRecoveryEmail`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        email: document.getElementById("registeredEmail").value
      })
    })
      .then(res => {
        return res.json();
      })
      .then(result => {
        if (result.success) {
          showAlert("OTP has been successfully sent to your registered email address.");
          localStorage.setItem("verificationModule", JSON.stringify(result.verificationModule));
          setIsEmailSent(true);
        } else {
          showAlert(result.message);
        }
      });
  }

  const updateNewPasswordAction = e => {
    e.preventDefault();
    if(!localStorage.getItem("verificationModule")){
      showAlert('Kindly send recovery email in order to reset your account password!')
      return
    }
    // Update new password api
    if(newCredentials.newPassword === newCredentials.confirmPassword){
      fetch(`${host}/auth/updateRecoveryPassword`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          newPassword: newCredentials.newPassword,
          otp: newCredentials.otpCode,
          verificationModule: JSON.parse(localStorage.getItem("verificationModule"))
        })
      })
        .then(res => {
          return res.json();
        })
        .then(result => {
          if (result.success) {
            showAlert("Your password has been updated succesfully!");
            localStorage.removeItem("verificationModule");
            history.push("/login");
          } else {
            showAlert(result.message);
          }
        });
    }
  };

  return <div className="recoverPassword">
      <div className="recoverPasswordModal">
        <img src={logo} alt="logo" className="recoverPasswordLogo" />
        <p className="recoverPasswordHeading">
          {isEmailSent ? "Reset Pasword" : "Send Recovery Email"}
        </p>
        <p className="recoverPasswordHelp">
          {isEmailSent ? "Create New Password" : "Enter your registered email and send recovery email."}
        </p>
        {/* Send otp to the registered email */}
        <div className="sendOtp" style={{ display: !isEmailSent ? "block" : "none" }}>
          <form className="recoverPassswordForm" onSubmit={sendOtpEmailAction}>
            <div className="profilePasswordLabel recoveryInputFields">
              <input placeholder="Registered email address" required name="email" type="email" className="inputFields fullWidth" id="registeredEmail" />
            </div>
            <button className="sendOtpBtn recoveryBtns" type="submit">
              Send Recovery Email
            </button>
          </form>
        </div>
        {/* Create new password */}
        <div className="createPassword" style={{ display: isEmailSent ? "block" : "none" }}>
          <form className="recoverPassswordForm" onSubmit={updateNewPasswordAction}>
            <div className="profilePasswordLabel recoveryInputFields">
              <input placeholder="Enter OTP code here" maxLength={6} minLength={6} required value={newCredentials.otpCode} name="otpCode" onChange={changeDetails} type="text" className="inputFields fullWidth" />
            </div>
            <div className="profilePasswordLabel recoveryInputFields">
              <input placeholder="New password" minLength={5} required value={newCredentials.newPassword} name="newPassword" onChange={changeDetails} type="password" id="newPassword" className="inputFields fullWidth" />
              <i onClick={seePassword} className="seeSignUpPassword fa fa-eye-slash" />
            </div>
            <div className="profilePasswordLabel recoveryInputFields">
              <input placeholder="Confirm new password" required value={newCredentials.confirmPassword} name="confirmPassword" onChange={changeDetails} type="password" className={`inputFields fullWidth ${newCredentials.newPassword !== newCredentials.confirmPassword ? "warning" : ""}`} />
              <i onClick={seePassword} className="seeSignUpPassword fa fa-eye-slash" />
              {newCredentials.newPassword !== newCredentials.confirmPassword && <small className="warningMessage passwordWarning">
                  Doesn't match with new password
                </small>}
            </div>
            <button disabled={newCredentials.newPassword !== newCredentials.confirmPassword} className="resetPasswordBtn recoveryBtns" type="submit">
              Reset Password <i className="fa fa-refresh" aria-hidden="true" />
            </button>
          </form>
        </div>
        <p className="sendEmailLink" onClick={toggleRecoveryOption}>
          {isEmailSent ? "Send recovery email" : "Already have OTP code"}
        </p>
      </div>
    </div>;
};

export default RecoverPassword;
