import loginSideMascot from "../assests/Images/loginSideMascot.svg";
import loginMascot from "../assests/Images/loginMascot.svg";
import { Link } from "react-router-dom";
import { useState, useRef, useContext } from "react";
import { useHistory } from "react-router-dom";
import ProductContext from "../contexts/products/ProductContext";

const Login = () => {
    const context = useContext(ProductContext);
    const { host, setUser, showAlert } = context;
    const history = useHistory();
    const ref = useRef();

    const [loginDetails, setLoginDetails] = useState({
      email: "",
      password: ""
    });

    const changeDetails = (e) => {
      setLoginDetails({ ...loginDetails, [e.target.name]: e.target.value });
    };

    const seePassword = (e) => {
    let passwordField = e.target.parentNode.children[0];
    // console.log(passwordField);
    if (e.target.classList.contains("fa-eye-slash")) {
      e.target.classList.replace("fa-eye-slash", "fa-eye");
      passwordField.type = "text";
    } else {
      e.target.classList.replace("fa-eye", "fa-eye-slash");
      passwordField.type = "password";
    }
  };

  const loginAction = (e)=>{
    e.preventDefault()
    //  Submitting form
    fetch(`${host}/auth/login/`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(loginDetails)
    })
      .then((res) => {
        return res.json();
      })
      .then((result) => {
        if (result.success) {
          setUser(result.user);
          showAlert("You've logged in to your profile");
          localStorage.setItem("auth-token", result.authtoken);
          setTimeout(() => {
            history.push("/");
            window.location.reload();
          }, 300);
        } else {
          showAlert(result.message);
        }
      });
  }
  
  return (
    <div className="signUpContainer">
      <div className="loginMainContainer">
        <div className="signUpFormContainer">
          <Link to='/recoverPassword' className="forgotPasswordLink">Forgot password?</Link>
          <button
            onClick={() => {
              ref.current.click();
            }}
            className="signupLoginBtns"
            id="loginButton"
          >
            Login
          </button>
          <h2 className="signUpHeading center">Login to your profile!</h2>
          <div className="loginMascot">
            <img id="mobileLoginMascot" src={loginMascot} alt="" />
          </div>
          <form onSubmit={loginAction} className="singUpForm">
            <button
              ref={ref}
              style={{ display: "none" }}
              type="submit"
            ></button>
            <div className="inputsSignUp">
              <label className="labelGroup" htmlFor="loginEmail">
                Email
              </label>
              <input
                required
                name="email"
                value={loginDetails.email}
                type="email"
                className="inputFields fullWidth"
                id="loginEmail"
                onChange={changeDetails}
              />
            </div>
            <div className="inputsSignUp">
              <label className="labelGroup" htmlFor="loginPassword">
                Password
              </label>
              <div className="profilePasswordLabel">
                <input
                  minLength={5}
                  required
                  value={loginDetails.password}
                  name="password"
                  onChange={changeDetails}
                  type="password"
                  id="loginPassword"
                  className="inputFields fullWidth"
                />
                <i
                  onClick={seePassword}
                  className="seeSignUpPassword fa fa-eye-slash"
                ></i>
              </div>
            </div>
          </form>
          <div className="alreadyUserMobile">
            <Link to="/signup">Sign up for a new account</Link>
          </div>
        </div>
        <div className="mascot">
          <h3 className="mascotHeading">Buy, Sell, Repeat</h3>
          <img src={loginSideMascot} alt="" className="loginMascotImg" />
          <div className="alreadyUser">
            <p>Create a free account</p>
            <Link to="/signup">
              <button>Sign up</button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
