const Notification = (props) => {
  const { notifItem, index, markAsRead } = props;

  return (
    <div className="notif">
      <p className="message">
        {notifItem.message}
        <br /> Regards, <br /> FETN team
      </p>
      <small className="time">
        Recieved on {notifItem.time}
        <span onClick={()=>{markAsRead(index, 1)}} className="markRead">
          Mark as read
          <i className="fa fa-check-square-o" aria-hidden="true"></i>
        </span>
      </small>
    </div>
  );
};

export default Notification;
