import "../results.css"
import { useContext } from 'react';
import ProductContext from '../contexts/products/ProductContext';
import { useHistory } from "react-router";


const ListItem = (props) => {
    const context = useContext(ProductContext)
    const history = useHistory()
    const { productItem } = props
    const { productPicturePath } = context;

    return (
        <div className="listItem" onClick={() => {history.push("/productDetail?product=" + productItem._id);}}>
            <div className="listItemImageDiv">
                <img className="listItemImage" src={productPicturePath + productItem.productImages[0]}  alt="" />
            </div>
            <div className="listItemContent">
                <div className="itemName"> {productItem.name} </div>
                <hr />
                <div style={{margin: "7px 0px"}}>
                    <span className="priceDetails">{productItem.sellingPrice}</span>
                    <span className="detailMRP">{productItem.actualPrice}</span>
                    <span className="detailReduction">{Math.ceil(((productItem.actualPrice - productItem.sellingPrice) / productItem.actualPrice) * 100)}% off</span>
                </div>
                <p className="listDetails">Category: {productItem.category}</p>
                <p className="listDetails">Availability: {productItem.availability}</p>
                <p className="listDetails">Quantity: {productItem.quantity}</p>
                <p className="listDetails" style={{ paddingBottom: "4px" }}>Listed on: {new Date(Number(productItem.date)).getDate() + "/" + Number(new Date(Number(productItem.date)).getMonth()+1) + "/" + new Date(Number(productItem.date)).getFullYear()}</p>
                <hr />
                <p className="listDesc"> {productItem.description}</p>
            </div>
        </div>
    )
}

export default ListItem
