import React from 'react'
// import img from "../assests/Images/waitingGif.gif"
import "../waiting.css"
import background from "../assests/Images/No_results/bg.svg"
import submarine from "../assests/Images/No_results/submarine.svg"
import fish_1 from "../assests/Images/No_results/Fish_1.svg"
import fish_2 from "../assests/Images/No_results/Fish_2.svg"

const NotFound = () => {
    return (
        <div className="notFoundDiv">
            {/* <p>No matches found</p>
            <img id="submarine" src={img} alt="" /> */}
            <img src={background} alt="" id="bg" />
            <img src={submarine} alt="" id="submarine" />
            <img src={fish_1} alt="" id="fish_1" />
            <img src={fish_2} alt="" id="fish_2" />
        </div>
    )
}

export default NotFound
