import './App.css';
import BottomNav from './components/BottomNav';
import Navbar from './components/Navbar';
import { useContext } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Home from './components/Home';
import About from './components/About';
import RegisterProduct from './components/RegisterProduct';
import { Alert } from './components/Alert';
import ProductDetail from './components/ProductDetail';
import ProductContext from './contexts/products/ProductContext';
import Results from './components/Results';
import Searching from "../src/components/Searching"
import Wishlist from './components/Wishlist';
import ExploreMore from './components/ExploreMore';
import Sellings from './components/Sellings';
import Profile from "./components/Profile"
import Signup from './components/Signup';
import Login from './components/Login';
import Logout from './components/Logout';
import RecoverPassword from './components/RecoverPassword';

const App = () => {
  // for api requests
  const context = useContext(ProductContext)
  const { alertMessage, showAlert, dismissAlert, host, user } = context


  // useEffect(() => {
  //   if (!user) {
  //     fetchUser();
  //   }
  //   // if(user){
  //   //   setDisplayPicture(user.displayPicture);
  //   //   setIsLoggedIn(true);
  //   // }
  //   // eslint-disable-next-line
  // }, [])


  return (
    <>
      <Router>
        <Navbar />
        <Alert alertMessage={alertMessage} dismissAlert={dismissAlert} />
        <Switch>
          <Route exact path="/">
            <Home user={user} host={host} showAlert={showAlert} />
          </Route>
          <Route exact path="/about">
            <About />
          </Route>
          <Route exact path="/signup">
            <Signup />
          </Route>
          <Route exact path="/productDetail">
            <ProductDetail />
          </Route>
          <Route exact path="/register-your-product">
            <RegisterProduct host={host} showAlert={showAlert} />
          </Route>
          <Route exact path="/results">
            <Results />
          </Route>
          <Route exact path="/searching">
            <Searching />
          </Route>
          <Route exact path="/wishlist">
            <Wishlist />
          </Route>
          <Route exact path="/exploreMore">
            <ExploreMore />
          </Route>
          <Route exact path="/yourSellings">
            <Sellings />
          </Route>
          <Route exact path="/profile">
            <Profile/>
          </Route>
          <Route exact path="/login">
            <Login />
          </Route>
          <Route exact path="/logout">
            <Logout />
          </Route>
          <Route exact path="/recoverPassword">
            <RecoverPassword />
          </Route>
        </Switch>
        <BottomNav />
      </Router>
    </>
  );
}

export default App;
