import { useContext, useState, useEffect } from "react";
import ProductContext from "../contexts/products/ProductContext";
import "../productDetail.css";
import ProductTile from "./ProductTile";
import def from "../assests/Images/userDefault.png";
import { useLocation } from "react-router-dom";
import Share from "./Share";

const ProductDetail = (props) => {
  const [productItem, setProductItem] = useState(null);
  const [moreProducts, setMoreProducts] = useState(null);
  const [sellerUser, setSellerUser] = useState(null);
  const [date, setDate] = useState(null);
  const location = useLocation();

  // console.log(location.search.indexOf("%22"));
  // console.log(location.search.lastIndexOf("%22"));
  // console.log(
  //   location.search.slice(
  //     location.search.indexOf("=") +1
  //   )
  // );
  // state for wishlist

  const context = useContext(ProductContext);
  var { showAlert, user, setUser, host, notifyUser, productPicturePath, userPicturePath } = context;
  const detailProductId = location.search.slice(
    location.search.indexOf("=") + 1
  );
  // console.log(detailProductId);
  //   if (!detailProductId) {
  //     detailProductId = localStorage.getItem("detailProductId");
  //   }

  const updateWishlist = () => {
    // console.log(user.wishlist.includes(detailProductId));
    fetch(`${host}/auth/addToWishlist`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem("auth-token")
      },
      body: JSON.stringify({ productId: detailProductId })
    })
      .then((res) => {
        return res.json();
      })
      .then((result) => {
        // console.log(result);
        if (result.success) {
          setUser(result.user);
          showAlert("Wishlist updated!");
        }
      });
  };


  const updateReports = () => {
    let reportCategory = document.getElementById("reportCategory");
    let reportsContainer = document.getElementById("reportsContainer");

    // console.log(reportCategory.value);
    // Submit report through api
    fetch(`${host}/products/reportproduct/${detailProductId}`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem("auth-token")
      },
      body: JSON.stringify({ report: reportCategory.value })
    })
      .then((res) => {
        return res.json();
      })
      .then((result) => {
        // console.log(result);
        if (result.success) {
          setProductItem(result.updatedProduct);
          reportsContainer.style.display = "none";
          if (result.message === "Product reported successfully") {
            if (productItem.reportsCount + 1 > 10) {
              notifyUser(
                `Hello, ${
                  sellerUser.name.split(" ")[0]
                }. One more report has been generated against your product ${
                  productItem.name
                }. User has reported it as ${
                  reportCategory.value
                }. Your product is getting blacklisted due to mass reports. In total ${
                  productItem.reportsCount + 1
                } report(s) have been generated against the same. Contact us in case of a spam reports.`,
                sellerUser
              );
            } else {
              notifyUser(
                `Hello, ${
                  sellerUser.name.split(" ")[0]
                }. A report has been generated against your  product ${
                  productItem.name
                }. User has reported it as ${reportCategory.value}. ${
                  productItem.reportsCount + 1
                } report(s) have been generated in total against the same. In case total reports exceeds 10, the product will be blacklisted. Kindly check and update the product details if required or you can contact us in case of a fake report.`,
                sellerUser
              );
            }
          } else {
            notifyUser(
              `Hello, ${
                sellerUser.name.split(" ")[0]
              }. The report against your product ${
                productItem.name
              } has been taken down. Your product has now total ${
                productItem.reportsCount - 1
              } report(s).`,
              sellerUser
            );
          }
          showAlert("Thanks for the feedback!");
        }
      });
  };

  const openReports = () => {
    let reportsContainer = document.getElementById("reportsContainer");
    if (productItem.reportedBy.includes(user._id)) {
      return updateReports();
    }
    reportsContainer.style.display = "block";
  };

  const fetchProductsFromSameSeller = (id) => {
    fetch(`${host}/products/getProductsBySeller/${id}`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem("auth-token")
      }
    })
      .then((res) => {
        return res.json();
      })
      .then((result) => {
        // console.log(result);
        if (result.success) {
          setMoreProducts(result.products);
        }
      });
  };

  const fetchSellerDetails = (id) => {
    // Fetch seller info from result.product.seller
    fetch(`${host}/auth/getUser/${id}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem("auth-token")
      }
    })
      .then((res) => {
        return res.json();
      })
      .then((result) => {
        // console.log(result);
        if (result.success) {
          fetchProductsFromSameSeller(id);
          setSellerUser(result.user);
        }
      });
  };
  // Fetching the product with id
  useEffect(() => {
    if (detailProductId) {
      localStorage.setItem("detailProductId", detailProductId);
      fetch(`${host}/products/getproduct/${detailProductId}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then((res) => {
          return res.json();
        })
        .then((result) => {
          // console.log(result);
          if (result.success) {
            setProductItem(result.product);
            let d = new Date(Number(result.product.date));
            // console.log(typeof(result.product.date));
            let dt = d.getDate();
            let mon = d.getMonth() + 1;
            let yr = d.getFullYear();
            setDate(dt + "/" + mon + "/" + yr);
            // console.log(result.product.seller);
            fetchSellerDetails(result.product.seller);
          }
        });
    }
    setTimeout(() => {
      // console.log(document.getElementsByClassName("eapps-link")[0]);
      document.getElementsByClassName("eapps-link")[0].style.display = "none"
    }, 2000);
    // eslint-disable-next-line
  }, [detailProductId]);

  const imageSelection = (e) => {
    // console.log(e.target);
    let allImages = document.getElementsByClassName("allImages");
    allImages = Array.from(allImages);
    allImages.forEach((element) => {
      element.classList.remove("selected");
    });
    e.target.classList.add("selected");
    let previngImage = document.getElementById("previngImage");
    previngImage.src = e.target.src;
  };
  let i = 0;

  return (
    <>
      {productItem && (
        <div className="detailsContainer">
          <div className="imagesContainer">
            <div className="previewer">
              <img
                src={`${productPicturePath}${productItem.productImages[0]}`}
                alt="Preview images"
                id="previngImage"
              />
            </div>
            <div className="images">
              {/* <img onMouseOver={imageSelection} src={productItem.productImages[0]} alt="" className="allImages selected" /> */}
              {productItem.productImages.map((e, i) => {
                if (i === 0) {
                  return (
                    <img
                      key={i}
                      onMouseOver={imageSelection}
                      src={`${productPicturePath}${e}`}
                      alt=""
                      className="selected allImages"
                    />
                  );
                }
                return (
                  <img
                    key={i}
                    onMouseOver={imageSelection}
                    src={`${productPicturePath}${e}`}
                    alt=""
                    className="allImages"
                  />
                );
              })}
            </div>
          </div>
          <div className="aboutProductContainer">
            <div className="productDetailHeading">
              <p className="productDetailName">{productItem.name}</p>
              <p className="productDetailDesc">{productItem.description}</p>
            </div>
            <div className="otherDetails">
              <p className="buyAt">Selling price</p>
              <div
                style={{
                  display: "flex",
                  alignItems: "baseline",
                  justifyContent: "space-between"
                }}
              >
                <div>
                  <span className="priceDetails">
                    {productItem.sellingPrice}
                  </span>
                  <span className="detailMRP">{productItem.actualPrice}</span>
                  <span className="detailReduction">
                    {Math.ceil(
                      ((productItem.actualPrice - productItem.sellingPrice) /
                        productItem.actualPrice) *
                        100
                    )}
                    % off
                  </span>
                </div>
                {sellerUser && (
                  <button
                    onClick={updateWishlist}
                    className={`addToCart ${
                      user.wishlist.includes(detailProductId)
                        ? "wishlisted"
                        : "notWishlisted"
                    }`}
                  >
                    {" "}
                    {!user.wishlist.includes(detailProductId) ? (
                      <>
                        <i className="fa fa-add"></i> wishlist{" "}
                        <i className="fa fa-shopping-cart"></i>
                      </>
                    ) : (
                      "Wishlisted"
                    )}
                  </button>
                )}
              </div>
            </div>
            <div className="otherDetails">
              <p
                style={{
                  fontWeight: "bold",
                  fontSize: "15px",
                  marginBottom: "4px"
                }}
              >
                Other details -
              </p>
              <div className="detailsGrid">
                <p className="highlightsKey">Category </p>
                <p className="highlightsValue">{productItem.category}</p>
                <p className="highlightsKey">Quantity </p>
                <p className="highlightsValue"> {productItem.quantity}</p>
                <p className="highlightsKey">Status </p>
                <p className="highlightsValue">{productItem.availability}</p>
                <p className="highlightsKey">Listed on </p>
                <p className="highlightsValue">{date}</p>
              </div>
              {sellerUser && (
                <div>
                  <div style={{ display: "flex", justifyContent: "flex-end" }}>
                    <button
                      onClick={openReports}
                      className={`reportBtn ${
                        productItem.reportedBy.includes(user._id)
                          ? "submitted"
                          : "notSubmitted"
                      }`}
                    >
                      {productItem.reportedBy.includes(user._id)
                        ? "Reported"
                        : "Report product"}
                    </button>
                  </div>
                  <div style={{ display: "none" }} id="reportsContainer">
                    <select name="reportCategory" id="reportCategory">
                      <option value="Not available for sale">
                        Not available for sale
                      </option>
                      <option value="Inappropriate item on sale">
                        Inappropriate item on sale
                      </option>
                      <option value="Misleading users">Misleading users</option>
                      <option value="Inaccurate information">
                        Inaccurate information
                      </option>
                    </select>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "flex-end",
                        marginTop: "5px"
                      }}
                    >
                      <button
                        style={{ background: "#d64550" }}
                        onClick={updateReports}
                        className="reportBtn"
                      >
                        {" "}
                        Submit{" "}
                      </button>
                    </div>
                  </div>
                </div>
              )}
            </div>
            {moreProducts && (
              <div className="otherDetails">
                <h3 className="containerTitle">Also selling -</h3>
                <div className="otherProducts">
                  {moreProducts.map((e) => {
                    i++;
                    if (i <= 2) {
                      if (e.availability === "available") {
                        return <ProductTile key={e._id} productItem={e} />;
                      }
                      i--;
                      return null;
                    }
                    return null;
                  })}
                </div>
              </div>
            )}
          </div>
          <div className="aboutSellerContainer">
            <p style={{ padding: "5px", fontSize: "17px" }}>Contact seller</p>
            <hr />
            <div className="sellerImgDiv">
              <img
                src={
                  sellerUser
                    ? `${userPicturePath}${sellerUser.displayPicture}`
                    : def
                }
                alt=""
                id="sellerImage"
              />
            </div>
            <p
              style={{ fontSize: "20px", padding: "5px", textAlign: "center" }}
            >
              {sellerUser ? sellerUser.name : "Login to get full access"}{" "}
            </p>
            <hr />
            {sellerUser && (
              <div>
                <div className="detailBox">
                  <a
                    rel="noreferrer"
                    target="_blank"
                    className="generalInfo"
                    href={`mailto:${sellerUser.email}`}
                  >
                    {" "}
                    <i
                      style={{ fontSize: "16px", marginRight: "4px" }}
                      className="fa fa-envelope"
                    ></i>{" "}
                    {sellerUser.email}
                  </a>
                </div>
                <hr />
                <div className="detailBox">
                  <a
                    rel="noreferrer"
                    target="_blank"
                    className="generalInfo"
                    href={`tel:${sellerUser.contact}`}
                  >
                    {" "}
                    <i
                      style={{ fontSize: "16px", marginRight: "4px" }}
                      className="fa fa-phone"
                    ></i>{" "}
                    {sellerUser.contact}
                  </a>
                </div>
                <hr />
                <div className="detailBox">
                  <a
                    rel="noreferrer"
                    target="_blank"
                    className="generalInfo"
                    href={`https://wa.me/${sellerUser.contact}`}
                  >
                    {" "}
                    <i
                      style={{ fontSize: "16px", marginRight: "4px" }}
                      className="fa fa-whatsapp"
                    ></i>{" "}
                    {sellerUser.contact}
                  </a>
                </div>
                <hr />
                <div className="detailBox">
                  <p className="generalInfo">
                    {" "}
                    <i className="fa fa-map-marker"></i>{" "}
                    {sellerUser.city[0].toUpperCase() +
                      sellerUser.city.substr(1, sellerUser.city.length - 1)}
                  </p>
                </div>
              </div>
            )}
          </div>
        </div>
      )}
      <Share />
    </>
  );
};

export default ProductDetail;
