import { useContext } from 'react'
import "../tiles.css"
import ProductContext from '../contexts/products/ProductContext';
import { useHistory } from 'react-router-dom';

const ProductTile = (props) => {
    const {productItem} = props
    const context = useContext(ProductContext)
    const { productPicturePath } = context;
    const history = useHistory();
    
    return (
        <>
            {productItem ? <div className="tile" onClick={() => { history.push("/productDetail?product=" + productItem._id);}}>
                <div className="imgContianer">
                    <img src={productPicturePath + productItem.productImages[0]} alt="" className="tileImg" />
                </div>
                <div className="tileInfo">
                    <p className="pricings">{productItem.sellingPrice} <span className="MRP">{productItem.actualPrice}</span></p>
                    <p className="productName">{productItem.name}</p>
                    <p className="productDescription">{(productItem.description.length <= 45) ? productItem.description : productItem.description.substr(0,45) + "..."} </p>
                </div>
            </div> : "Searching..."}
        </>
    )
}

export default ProductTile
