import { useState, useContext, useEffect } from "react";
import ProductContext from "../contexts/products/ProductContext";
import ListItem from "./ListItem";
import "../results.css";
import NotFound from "./NotFound";
import InfiniteScroll from "react-infinite-scroll-component";
import Loader from "./Loader";
import { useLocation } from "react-router-dom";

const Results = (props) => {
  const context = useContext(ProductContext);
  const { user, showAlert, host } = context;
  const [products, setProducts] = useState([]);
  const [totalResults, setTotalResults] = useState(0);
  const [loading, setLoading] = useState(true);
  const [starting, setStarting] = useState(0);
  const location = useLocation();

  const sorting = location.search.slice(location.search.indexOf("=") + 1);

  const fetchMore = () => {
    setLoading(true);
    // console.log(starting);
    if (sorting === "institution") {
      fetch(`${host}/products/getProductsByInstitution/${user.institution}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ limit: 5, startFrom: starting })
      })
        .then((res) => {
          return res.json();
        })
        .then((result) => {
          if (result.success) {
            // console.log(result.products);
            setProducts(products.concat(result.products));
            setTotalResults(result.resultsCount);
          } else {
            showAlert(result.message);
          }
        });
    } else if (sorting === "query") {
      let searchQuery = location.hash.slice(location.hash.indexOf("#") + 1);
      searchQuery = searchQuery.replace(/%20/g, " ");
      // console.log(searchQuery);
      fetch(`${host}/products/searchProducts`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ query: searchQuery })
      })
        .then((res) => {
          return res.json();
        })
        .then((result) => {
          if (result.success) {
            setProducts(result.products);
            setTotalResults(result.resultsCount);
          } else {
            showAlert(result.message);
          }
        });
    } else {
      // console.log(sorting);
      fetch(`${host}/products/getSortedProducts`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          sortingType: sorting,
          limit: 5,
          startFrom: starting
        })
      })
        .then((res) => {
          return res.json();
        })
        .then((result) => {
          if (result.success) {
            // console.log(result.products);
            setProducts(products.concat(result.products));
            setTotalResults(result.resultsCount);
          } else {
            showAlert(result.message);
          }
        });
    }
    setLoading(false);
    setStarting(starting + 5);
  };

  useEffect(() => {
    if (products.length < 1) {
      fetchMore();
    }
  }, []);

  return (
    <>
      <InfiniteScroll
        dataLength={products.length} //This is important field to render the next data
        next={fetchMore}
        hasMore={totalResults !== products.length}
        loader={<Loader />}
      >
        {loading && <Loader />}
        {products.length > 0 ? (
          <div className="listItemsContainer">
            <p
              className="searchResultsHeading"
            >
              Search results{" "}
            </p>
            {products.map((productItem) => {
              if (productItem.availability === "available") {
                return (
                  <ListItem key={productItem._id} productItem={productItem} />
                );
              }
              return null;
            })}
          </div>
        ) : (
          <NotFound />
        )}
      </InfiniteScroll>
      {/* <NotFound /> */}
    </>
  );
};

export default Results;
