import React, { useState, useEffect, useContext } from 'react'
import ProductTile from './ProductTile'
import { Link } from 'react-router-dom';
import ProductContext from '../contexts/products/ProductContext';


const ProductContainer = (props) => {
    const [products, setProducts] = useState([]);
    const { user, host, showAlert, sortBy } = props;
    // const [sameInstituteProducts, setSameInstituteProducts] = useState([])
    let i = 0;
    // let temp = [];

    // Context
    // const context = useContext(ProductContext)
    // const { setSorting } = context;

    useEffect(() => {
        if (products.length < 1) {
            // console.log(sortBy);
            if (sortBy === "institution") {
                fetch(`${host}/products/getProductsByInstitution/${user.institution}`, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({ limit: 4 })
                }).then((res) => {
                    return res.json();
                }).then((result) => {
                    if (result.success) {
                        // console.log(result.products);
                        setProducts(result.products);
                    }
                    else {
                        showAlert(result.message);
                    }
                })
            }
            else {
                fetch(`${host}/products/getSortedProducts`, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({ sortingType: sortBy, limit: 4 })
                }).then((res) => {
                    return res.json();
                }).then((result) => {
                    if (result.success) {
                        // console.log(result.products);
                        setProducts(result.products);
                    }
                    else {
                        showAlert(result.message);
                    }
                })
            }
        }
        // eslint-disable-next-line
    }, [])

    return (
        <>
            {products.length > 0 && <div className={`ProductContainer ProductContainer_${props.containerNumber}`}>
                <div className="tileTitleContainer">
                    <h3 className="containerTitle">{props.title}</h3>
                    <Link to={`/results?sortby=${sortBy}`} className="viewMore" href="/results">View more</Link>
                </div>
                <div className="productGrid">
                    {products.map((productItem) => {
                        i++;
                        if (i <= 4) {
                            if (productItem.availability === "available") {
                                return <ProductTile key={productItem._id} productItem={productItem} />
                            }
                            i--;
                            return null
                        }
                        return null
                    })}
                </div>
            </div>}
        </>
    )
}

export default ProductContainer
