import {useHistory, useLocation} from "react-router-dom"
import '../../src/bottomNav.css'

const BottomNav = () => {
    const history = useHistory();
    const location = useLocation();
    
    return (
        <div className="bottomNav">
            {/* <div id="bar"></div> */}
            <div onClick={()=>{history.push("/")}} className="navTile"><i className={`fa fa-home navItems ${location.pathname==="/"?'navItemsClicked':'navItemsClickedNotClicked'}`}></i><span className={`tileText ${location.pathname==="/"?"tileTextClicked":"tileTextNotClicked"}`}> Home</span></div>
            
            <div onClick={()=>{history.push("/wishlist")}} className="navTile"><i className={`fa fa-shopping-cart navItems ${location.pathname==="/wishlist"?'navItemsClicked':'navItemsClickedNotClicked'}`}></i><span className={`tileText ${location.pathname==="/wishlist"?"tileTextClicked":"tileTextNotClicked"}`}> Wishlist</span></div>
            
            <div onClick={()=>{history.push("/exploreMore")}} className="navTile"><i className={`fa fa-bars navItems ${location.pathname==="/exploreMore"?'navItemsClicked':'navItemsClickedNotClicked'}`}></i><span className={`tileText ${location.pathname==="/exploreMore"?"tileTextClicked":"tileTextNotClicked"}`}> More</span></div>
        </div>
    )
}

export default BottomNav
