import React from 'react'
import close from "../assests/Images/close.svg"

export const Alert = (props) => {
    return (
        <>
        {props.alertMessage && <div className="alertBox">
            <div className="alertMessage">
                    {props.alertMessage}
            </div>
            <div id="dismissMessage">
                    <img src={close} alt="" onClick={props.dismissAlert}/>
            </div>
        </div>}
        </>
    )
}
