import ProductContainer from './ProductContainer'

const Home = (props) => {
    const { user, host, showAlert } = props;
    // console.log(user);

    return (
        <div className="main">
            <ProductContainer containerNumber="1" title="Best deals to pick!" sortBy="sellingPrice" host={host} showAlert={showAlert} />
            <ProductContainer containerNumber="3" sortBy="discount" title="Biggest discounts!" host={host} showAlert={showAlert}/>
            {user && <ProductContainer containerNumber="2" title={`Deals from ${user.institution}`} sortBy="institution" host={host} showAlert={showAlert} user={user}/>}
            <ProductContainer containerNumber="1" title="Latest Deals" sortBy="date" host={host} showAlert={showAlert}/>
        </div>
    )
}

export default Home
